<template>
  <div id="stac-browser-auth-modal">
    <b-form @submit.stop.prevent="submit" @reset="reset">
      <b-card no-body :header="$t('authentication.title')">
        <b-card-body>
          <p>{{ $t('authentication.description') }}</p>
          <Description v-if="promptText" :description="promptText" />
          <b-form-input autofocus type="password" v-model.trim="token" :required="required" />
        </b-card-body>
        <b-card-footer>
          <b-button type="submit" variant="primary">{{ $t('submit') }}</b-button>
          <b-button type="reset" variant="danger" class="ms-3">{{ $t('cancel') }}</b-button>
        </b-card-footer>
      </b-card>
    </b-form>
  </div>
</template>

<script>
import Description from '../Description.vue';
import { mapGetters } from 'vuex';
import { BCard, BCardBody, BCardFooter } from 'bootstrap-vue-next';

export default {
  name: 'ApiKey',
  components: {
    Description,
    BCard,
    BCardBody,
    BCardFooter
  },
  props: {
    description: {
      type: String,
      default: null
    },
    credentials: {
      type: String,
      default: ''
    }
  },
  emits: ['submit', 'reset'],
  data() {
    return {
      token: '',
      required: true
    };
  },
  computed: {
    ...mapGetters('auth', ['isLoggedIn']),
    promptText() {
      if (this.description) {
        return this.description;
      }
      return this.$t('authConfig.description');
    }
  },
  beforeCreate() {
    if (this.isLoggedIn) {
      this.$emit('submit', null);
    }
  },
  created() {
    if (this.credentials) {
      this.token = this.credentials;
      this.required = false;
    }
  },
  methods: {
    reset() {
      this.$emit('reset');
    },
    async submit() {
      this.$emit('submit', this.token);
    }
  }
};
</script>
